import React , {useState} from "react";
import './Test.css'
import axios from 'axios'
import Exam from '../Exam/Exam'

function Test(props){
let answer = props.answer;
let numberOfQuestions = props.numberOfQuestions;
let bigData = props.bigData;
let typeOfExam = props.typeOfExam;
let name = props.name;
let time = props.time;
let dateLastTime = props.dateLastTime;
let degree = props.degree;
let [now,setNow] = useState('test');
let [err,setErr] = useState(null);


    let x = 0;

    let debug = ()=>{
        for(let i=0;i<numberOfQuestions;i++){
            
            if(bigData[i].question === '' || answer[i].value === ''){
                
                if(bigData[i].question === ''){
                        setErr('   ')
                }else if(answer[i].value ===''){
                    setErr('   ')
                }
            }else{
                x +=1;
                setErr(null)
            }
            
          }
    }
    
    let saveExam =()=>{     
        debug();
              if(x === +numberOfQuestions){
                document.body.style.opacity = 0.9
                document.getElementById('loader').style.display = 'flex'
                let teacher = JSON.parse(localStorage.getItem('teacher')).name  
                axios({
                    method:'post',
                    url:'/home/saveexam',
                    data:{
                        teacher:teacher,bigData:bigData,answers:answer,exmaName:name,degree:degree,time:time,dateLastTime:dateLastTime
                        }
                }).then(()=>{
                    document.body.style.opacity = 1
                    document.getElementById('loader').style.display = 'none'
                        setNow('exam')
                    
                })  
              }
            
    }

   



    let viewExam = ()=>{
        let exams = [];
            if(typeOfExam === 'MCQ'){
                for(let i=0;i<numberOfQuestions;i++){
                    exams.push({
                        number:i,value:(
                            <ul className="admin-test" key={i}>
                                <h1 className="available-exam">  {i+1}</h1>
                                <li><input type="text" onChange={(e)=>{bigData[i].question = e.target.value}} placeholder=" "/></li>
                                <li ><i onClick={()=>{
                                     answer[i].value = bigData[i].answer1
                                 document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }}
                                 className={`fas fa-circle-notch ${i}`}></i>
                                <input onChange={(e)=>{bigData[i].answer1 = e.target.value}} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                    answer[i].value = bigData[i].answer2
                                 document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${i}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }} className={`fas fa-circle-notch ${i}`}></i><input onChange={(e)=>{bigData[i].answer2 = e.target.value}} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                     answer[i].value = bigData[i].answer3
                                 document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[2].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${i}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }} className={`fas fa-circle-notch ${i}`}></i><input onChange={(e)=>{bigData[i].answer3 = e.target.value}} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                     answer[i].value = bigData[i].answer4
                                document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[3].classList.replace('fa-circle-notch','fa-circle')
                                }} className={`fas fa-circle-notch ${i}`}></i><input onChange={(e)=>{bigData[i].answer4 = e.target.value}} type="text" placeholder=""/></li>
                            </ul>
                        )
                    })
            }
            }else if(typeOfExam === 'T/F'){
                for(let i=0;i<numberOfQuestions;i++){
                    exams.push({
                        number:i,value:(
                            <ul className="admin-test" key={i}>
                                <h1 className="available-exam">  {i+1}</h1>
                                <li><input type="text" onChange={(e)=>{bigData[i].question = e.target.value}} placeholder=" "/></li>
                                <li ><i onClick={()=>{
                                answer[i].value = bigData[i].answer1
                                 document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle','fa-circle-notch')}} className={`fas fa-circle-notch ${i}`}></i><input onChange={(e)=>{bigData[i].answer1 = e.target.value}} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                    answer[i].value = bigData[i].answer2
                                 document.getElementsByClassName(`${i}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${i}`)[1].classList.replace('fa-circle-notch','fa-circle')}} className={`fas fa-circle-notch ${i}`}></i><input onChange={(e)=>{bigData[i].answer2 = e.target.value}} type="text" placeholder=""/></li>
                            </ul>
                        )
                    })
            }
            }
    
        return exams.map((e)=>{return e.value})
    }
       


            return(
                now === 'test' ? ( <>
                    <div className="admin-test2">
                        {
                            viewExam()
                        }
                    </div>
            <div className='div-apload'>

                        <button className="admin-test-save" onClick={async()=>{
                            saveExam();
                        }}></button>
                        </div>
                        {err ? <p className="nothing2">{err}</p>:null}
             </>) : <Exam/> 
            )
        
                        
}
export default Test