import React, { Component}  from 'react'
import './Exam.css'
import Test from '../Test/Test'
import axios from 'axios'
import ExamView from '../ExamView/ExamView'

class Exam extends Component{
    

  async  examsCaseChange(i){
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
        let name = JSON.parse(localStorage.getItem('teacher')).name
        let teacher = this.state.teacher;
         teacher.examsCase[i] = !teacher.examsCase[i];
         this.setState({teacher}) 
        
         
       await axios({
        method:'post',
        url:'/home/examscase',
        data:{name:name,examsCase:this.state.teacher.examsCase}
    }).then((res)=>{
        document.body.style.opacity = 1
        document.getElementById('loader').style.display = 'none'
        let arr = []; 
        
        this.setState({teacher:res.data.teacher})
        // print prev Exam
        if(res.data.teacher !== null){
            for(let i=0;i<res.data.teacher.examsNames.length;i++){
        
                arr.push({value: <>
                   <ul className="testo2-exam">
                            <li>{res.data.teacher.examsNames[i]}</li>
                            <li>{res.data.teacher.time[i] + "   "}</li>
                            <li>
                {` ${new Date(res.data.teacher.dateLastTime[i]).getSeconds() < 10 ? "0" +
                 new Date(res.data.teacher.dateLastTime[i]).getSeconds():new Date(res.data.teacher.dateLastTime[i]).getSeconds()}: 
                 ${ new Date(res.data.teacher.dateLastTime[i]).getMinutes() < 10 ? "0" +  new Date(res.data.teacher.dateLastTime[i]).getMinutes() :
                  new Date(res.data.teacher.dateLastTime[i]).getMinutes()} : ${new Date(res.data.teacher.dateLastTime[i]).getHours() > 12 ? 
                     new Date(res.data.teacher.dateLastTime[i]).getHours() > 10 ? new Date(res.data.teacher.dateLastTime[i]).getHours()-12 +
                      "    " :  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours()-12 + "    ": 
                      new Date(res.data.teacher.dateLastTime[i]).getHours() > 10?  new Date(res.data.teacher.dateLastTime[i]).getHours() +
                       "  ":  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours() + "    "  }  ,
                       ${new Date(res.data.teacher.dateLastTime[i]).getDate()}
                 ${this.state.months[new Date(res.data.teacher.dateLastTime[i]).getMonth()]} `}</li>
                            <li>{res.data.teacher.degree[i]}</li>
                            </ul>
                    {
                     (new Date(  this.state.teacher.dateLastTime[i]).getTime()/1000/60 ) - (new Date().getTime()/1000/60) < 0 
                     ? (<div className="btn-2"><button  onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>):this.state.teacher.examsCase[i] === false ?  (<div className="btn-2"><button 
                onClick={()=>{this.examsCaseChange(i)}}
                className="testo2-exam-btn" ></button><button onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>):(<div className="btn-2"><button 
                onClick={()=>{this.examsCaseChange(i)}}
                className="testo2-exam-btn2" ></button><button  onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>)}

                    </>})   
                }
                this.setState({preExams:arr})
        }
    })

    }

    componentDidMount(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let teacher = JSON.parse(localStorage.getItem('teacher')).name
        axios({
            method:'post',
            url:'/home/getexams',
            data:{
                teacher:teacher
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
                let arr = []; 
                this.setState({teacher:res.data.teacher})
                // print prev Exam
                if(res.data.teacher !== null){
                    for(let i=0;i<res.data.teacher.examsNames.length;i++){

                        arr.push({value: <>
                          
                            <ul className="testo2-exam">
                            <li>{res.data.teacher.examsNames[i]}</li>
                            <li>{res.data.teacher.time[i] + "   "}</li>
                            <li>
                {` ${new Date(res.data.teacher.dateLastTime[i]).getSeconds() < 10 ? "0" +
                 new Date(res.data.teacher.dateLastTime[i]).getSeconds():new Date(res.data.teacher.dateLastTime[i]).getSeconds()}: 
                 ${ new Date(res.data.teacher.dateLastTime[i]).getMinutes() < 10 ? "0" +  new Date(res.data.teacher.dateLastTime[i]).getMinutes() :
                  new Date(res.data.teacher.dateLastTime[i]).getMinutes()} : ${new Date(res.data.teacher.dateLastTime[i]).getHours() > 12 ? 
                     new Date(res.data.teacher.dateLastTime[i]).getHours() > 10 ? new Date(res.data.teacher.dateLastTime[i]).getHours()-12 +
                      "    " :  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours()-12 + "    ": 
                      new Date(res.data.teacher.dateLastTime[i]).getHours() > 10?  new Date(res.data.teacher.dateLastTime[i]).getHours() +
                       "  ":  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours() + "    "  }  ,
                       ${new Date(res.data.teacher.dateLastTime[i]).getDate()}
                 ${this.state.months[new Date(res.data.teacher.dateLastTime[i]).getMonth()]} `}</li>
                            <li>{res.data.teacher.degree[i]}</li>
                            </ul>
                            {
                     (new Date(  this.state.teacher.dateLastTime[i]).getTime()/1000/60 ) - (new Date().getTime()/1000/60) < 0 
                     ? (<div className="btn-2"><button onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>):this.state.teacher.examsCase[i] === false ?  (<div className="btn-2"><button 
                onClick={()=>{this.examsCaseChange(i)}}
                className="testo2-exam-btn" ></button><button onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>):(<div className="btn-2"><button 
                onClick={()=>{this.examsCaseChange(i)}}
                className="testo2-exam-btn2" ></button><button  onClick={()=>{this.setState({numberOfExam:i,numberOfI:res.data.teacher.exams[i].length,test:'show'})}}></button></div>)}
                        
                            </>})   
                        }
                        this.setState({preExams:arr})
                    }
        })
    }

    state  = {
        exam:[1],
        preExam:[1],
        stop:true,
        test:'exam',
        numberOfQuestions:1,
        typeOfExam:'T/F',
        name:'',
        degree:0,
        time:'',
        month:'',
        hour:'',
        min:'',
        dateLast:'',
        teacher:[],
        preExams:[],
        numberOfI:0,
        numberOfExam:0,
        months : ["","","","","","","","","","","",""]
    }
    
    render(){


    let testing = ()=>{
        if(this.state.test === 'exam'){
            return(
                <>
        <h2 className="available-exam">  :</h2>
                {
                    this.state.exam.length >=1 ? 
                    (
                        <>
                        <div className="admin-exam auto-hight">
                        <ul className="testo-exam">
                        <li></li>
                        <li> </li>
                        <li>  </li>
                        <li></li>
                        <li> </li>
                        <li> </li>
        
                    </ul>
                        <ul className="testo2-exam">
                        <li><input type="text" onChange={(e)=>{this.setState({name:e.target.value})}} placeholder="  ..."/></li>
                        <li><input type="number"min="0" defaultValue={this.state.time} onChange={(e)=>{this.setState({time:e.target.value})}} placeholder="...  "/></li>
                        <li>
                        <input type="date"  onChange={(e)=>{this.setState({month:e.target.value})}}   placeholder=""/>
                        <input type="number" min='0' max="24" onChange={(e)=>{this.setState({hour:e.target.value})}}   placeholder=""/>
                        <input type="number" min='0' max="60" onChange={(e)=>{this.setState({min:e.target.value})}}   placeholder=""/>
                        </li>
                        
                        <li><input type="number" min="0"  onChange={(e)=>{this.setState({degree:e.target.value})}}  defaultValue={this.state.degree} placeholder="  ..."/></li>
                        <li><input defaultValue={this.state.numberOfQuestions} min="1" onChange={(e)=>{this.setState({numberOfQuestions:e.target.value})}} type="number" placeholder="   ..."/></li>
                        <li><select className="type"><option>T/F</option><option>MCQ</option></select></li>
                         </ul>
                        </div>
            <div className='div-apload'>

                          <button className="testo2-exam-btn" onClick={()=>{
                              
                                    if(this.state.name && this.state.time && this.state.hour && this.state.min  && this.state.month !== ''){
                                        //type.current.value
                                        if(document.getElementsByClassName('type')[0].value === 'T/F'){
                                            this.setState({typeOfExam:'T/F'})
                                        }else if(document.getElementsByClassName('type')[0].value === 'MCQ'){
                                            this.setState({typeOfExam:'MCQ'})
                                        }
                                      this.setState({test:'test'})
                                    }    
                                    
                                
                                    let years = new Date(this.state.month).getFullYear();
                                    let months = new Date(this.state.month).getMonth();
                                    let days = new Date(this.state.month).getDate();
                                    let dateLastTime = new Date(`${months+1},${days},${years},${this.state.hour}:${this.state.min}:00`)
                                    this.setState({dateLast:dateLastTime})
                                   }}>  </button>
                                   </div>
                        </>
                    ):
                    (
                        <>
                        <p className="nothing2">   </p>
                         </>
                    )
                }


                    <h2 className="available-exam">   :</h2>
                {
                    this.state.preExams.length >=1  ?  
                    
                    <div className="admin-exam">
                    <ul className="testo-exam">
                    <li></li>
                    <li></li>
                    <li> </li>
                    <li></li>
                    </ul>
                    {this.state.preExams.map((e)=>{
                         return e.value
                     })}
                  </div>                 
                    :
                   (
                       <>
                       <p className="nothing2">   </p>
                        </>
                   )
                }  
                </>
            )
        }else if(this.state.test === 'test'){

            let bigData = []
            let answer = []
            for(let i=0;i<this.state.numberOfQuestions;i++){
                bigData.push({
                    question:'',
                    answer1:'',
                    answer2:'',
                    answer3:'',
                    answer4:'', 
                })
                answer.push({
                       value:'' 
                })
            }
            
       

   
            
            return<Test dateLastTime={this.state.dateLast} name={this.state.name} time={this.state.time} degree={this.state.degree} bigData={bigData} answer={answer} typeOfExam={this.state.typeOfExam} numberOfQuestions={this.state.numberOfQuestions}/>
        }else if(this.state.test === 'show'){
            return <ExamView teacher={this.state.teacher} answers={this.state.teacher} numberOfExam={this.state.numberOfExam} I={this.state.numberOfI}   numberOfQuestions={this.state.numberOfQuestions}  typeOfExam={this.state.typeOfExam}/>
        }
    }


    return(
        testing()
    )

    }
    
}

export default Exam