import React , {Component} from 'react'
import './Task.css'
import axios from 'axios'
class Task extends Component{
 
  state = {
    theTask:'',
    task:[1],
    taskName:'',
    lastDate:'',
    taskDegree:'',
    hour:'',
    min:'',
    allTasks:[] ,
    months : ["","","","","","","","","","","",""]
  }

  componentDidMount(){
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
    let name = JSON.parse(localStorage.getItem('teacher')).name
  
    axios({
      method:'post',
      url:'/home/gettask',
      data:{
        teacher:name
      }
    }).then((res)=>{
      document.body.style.opacity = 1
      document.getElementById('loader').style.display = 'none'
     let tasks = [];
      for(let i=0;i<res.data.teacher.taskAddedName.length;i++){
        tasks.push(
          {
            value:(
              <ul className="testo2-exam" key={Math.random()}>
              <li><input type="text" readOnly defaultValue={res.data.teacher.taskAddedName[i]}/></li>
              <li><input type="text" readOnly defaultValue={res.data.teacher.taskAdded[i]}/></li>
              <li><input type="text" readOnly defaultValue={` ${new Date(res.data.teacher.taskLastTime[i]).getSeconds() < 10 ? "0" + new Date(res.data.teacher.taskLastTime[i]).getSeconds():new Date(res.data.teacher.taskLastTime[i]).getSeconds()}: ${ new Date(res.data.teacher.taskLastTime[i]).getMinutes() < 10 ? "0" +  new Date(res.data.teacher.taskLastTime[i]).getMinutes() : new Date(res.data.teacher.taskLastTime[i]).getMinutes()} : ${new Date(res.data.teacher.taskLastTime[i]).getHours() > 12 ?  new Date(res.data.teacher.taskLastTime[i]).getHours() > 10 ? new Date(res.data.teacher.taskLastTime[i]).getHours()-12 + "    " :  "0" + new Date(res.data.teacher.taskLastTime[i]).getHours()-12 + "    ": new Date(res.data.teacher.taskLastTime[i]).getHours() > 10?  new Date(res.data.teacher.taskLastTime[i]).getHours() + "  ":  "0" + new Date(res.data.teacher.taskLastTime[i]).getHours() + "    "  }  , ${new Date(res.data.teacher.taskLastTime[i]).getDate() } ${this.state.months[new Date(res.data.teacher.taskLastTime[i]).getMonth()]} `}/></li>
              <li><input type="number" readOnly defaultValue={res.data.teacher.taskDegree[i]}/></li>
               </ul>
            )
          }
        )
        this.setState({allTasks:tasks})
      }
    })
  }

  getTask(){
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
    let name = JSON.parse(localStorage.getItem('teacher')).name
    axios({
      method:'post',
      url:'/home/gettask',
      data:{
        teacher:name
      }
    }).then((res)=>{
      document.body.style.opacity = 1
      document.getElementById('loader').style.display = 'none'
     let tasks = [];
      for(let i=0;i<res.data.teacher.taskAddedName.length;i++){
        tasks.push(
          {
            value:(
              <ul className="testo2-exam" key={Math.random()}>
              <li><input type="text" readOnly defaultValue={res.data.teacher.taskAddedName[i]}/></li>
              <li><input type="text" readOnly defaultValue={res.data.teacher.taskAdded[i]}/></li>
              <li><input type="text" readOnly defaultValue={` ${new Date(res.data.teacher.taskLastTime[i]).getSeconds() < 10 ? "0" + new Date(res.data.teacher.taskLastTime[i]).getSeconds():new Date(res.data.teacher.taskLastTime[i]).getSeconds()}: ${ new Date(res.data.teacher.taskLastTime[i]).getMinutes() < 10 ? "0" +  new Date(res.data.teacher.taskLastTime[i]).getMinutes() : new Date(res.data.teacher.taskLastTime[i]).getMinutes()} : ${new Date(res.data.teacher.taskLastTime[i]).getHours() > 12 ?  new Date(res.data.teacher.taskLastTime[i]).getHours() > 10 ? new Date(res.data.teacher.taskLastTime[i]).getHours()-12 + "    " :  "0" + new Date(res.data.teacher.taskLastTime[i]).getHours()-12 + "    ": new Date(res.data.teacher.taskLastTime[i]).getHours() > 10?  new Date(res.data.teacher.taskLastTime[i]).getHours() + "  ":  "0" + new Date(res.data.teacher.taskLastTime[i]).getHours() + "    "  }  , ${new Date(res.data.teacher.taskLastTime[i]).getDate() } ${this.state.months[new Date(res.data.teacher.taskLastTime[i]).getMonth()]} `}/></li>
              <li><input type="number" readOnly defaultValue={res.data.teacher.taskDegree[i]}/></li>
               </ul>
            )
          }
        )
        this.setState({allTasks:tasks,theTask:'',taskName:'',lastDate:'',taskDegree:'',hour:'',min:''})
      }
    })
  }
  render(){




    
    let handler = ()=>{ 
      document.body.style.opacity = 0.9
      document.getElementById('loader').style.display = 'flex'
      let years = new Date(this.state.lastDate).getFullYear();
      let months = new Date(this.state.lastDate).getMonth();
      let days = new Date(this.state.lastDate).getDate();
      let dateLastTime = new Date(`${months+1},${days},${years},${this.state.hour}:${this.state.min}:00`)
      this.setState({lastDate:dateLastTime})

      let email = JSON.parse(localStorage.getItem('teacher')).email
      let teacherName = JSON.parse(localStorage.getItem('teacher')).name
      axios({
          method:'post',
          url:'/home/addtask', 
          data:
          {
            teacherName:teacherName,email:email,tasklasttime:dateLastTime,taskDegree:this.state.taskDegree,name:this.state.taskName,task:this.state.theTask
          }
          
      }).then(()=>{
        document.body.style.opacity = 1
        document.getElementById('loader').style.display = 'none'
        this.getTask();
      })
      }
      
    return(
        <>
      {
          this.state.task.length >=1 ? 
          (
           

            <>
                <h1 className="admin-task-add">
                  :    
                </h1>
            <div className="admin-task">
            <div className="admin-exam auto-hight ">
            <ul className="testo-exam">
            <li> </li>
            <li></li>
            <li> </li>
            <li></li>

        </ul>
            <ul className="testo2-exam">
            <li><input type="text" value={this.state.taskName} onChange={(e)=>{this.setState({taskName:e.target.value})}}  placeholder="  ..."/></li>
            <li><input onChange={(e)=>{this.setState({theTask:e.target.value})}} type="text"/></li>
            <li><input type="date" value={this.state.lastDate}  onChange={(e)=>{this.setState({lastDate:e.target.value})}} placeholder="  ..."/>
            <input type="number" min='0' max="24" onChange={(e)=>{this.setState({hour:e.target.value})}} value={this.state.hour}   placeholder=""/>
            <input type="number" min='0' max="60" onChange={(e)=>{this.setState({min:e.target.value})}}  value ={this.state.min} placeholder=""/> 
            </li>
            <li><input type="number" value={this.state.taskDegree} onChange={(e)=>{this.setState({taskDegree:e.target.value})}} placeholder="  ..."/></li>
             </ul>
            </div>
            </div>
            <div className='div-apload'>

              <button className="testo2-exam-btn" onClick={()=>{
                if(this.state.taskName && this.state.lastDate && this.state.taskDegree){ handler()};
              }}></button>
              </div>
            </>

          ):( <p className="nothing2">  </p>)
      }



{
          this.state.task.length >=1 ? 
          (

            <>
                <h1 className="admin-task-add">
                  :   
                </h1>
            <div className="admin-task">
            <div className="admin-exam">
            <ul className="testo-exam">
            <li></li>
            <li> </li>
            <li> </li>
            <li></li>

        </ul>
          {
            this.state.allTasks.map((t)=>{
              return t.value
            })
          }
             
            </div>
            </div>
            </>

          ):( <p className="nothing2">    </p>)
      }
        </>
    )


    }

    
}

export default Task 