import React ,{Component} from "react";
import './ExamView.css'
import Exam from'../Exam/Exam'

class ExamView extends Component{


    state = {
        now:true,
        err:null
    }

    render(){
        let numberOfQuestions = this.props.I-1;
        let numberOfExam = this.props.numberOfExam
        let teacher = this.props.teacher
        // let answer = props.answer
        // let numberOfQuestions = this.props.numberOfQuestions
        // let bigData = props.bigData
        let typeOfExam = this.props.typeOfExam
        // let name = props.name
        // let time = props.time            `
        // let dateLastTime = props.dateLastTime
        // let degree = props.degree;
       

        let back = ()=>{
           if( this.state.now === true) 
            {
                   return ( <>
                    <div className="admin-test2">
                        {
                            viewExam()
                        }
                    </div>
            <div className='div-apload'>

                        <button className="admin-test-save" onClick={()=>{this.setState({now:false})}}></button>
                        <p>{this.state.err ? this.state.err:null}</p>
                        </div>
             </>) 
            }else{
            return <Exam/>
            } 
        }

        let viewExam = ()=>{
            let exams = [];
                if(typeOfExam === 'MCQ'){
                    for(let i=0;i<teacher.exams[numberOfQuestions].length;i++){
                   
                        
                        exams.push({
                            number:i,value:(
                                <ul className="admin-test" key={i}>
                                    <h1 className="available-exam">  {i+1}</h1>
                                    <li><input type="text" defaultValue={teacher.exams[numberOfExam][i].question} placeholder=" "/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer1 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input  type="text" defaultValue={teacher.exams[numberOfExam][i].answer1} placeholder=""/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer2 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input type="text" defaultValue={teacher.exams[numberOfExam][i].answer2} placeholder=""/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer3 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input type="text" defaultValue={teacher.exams[numberOfExam][i].answer3} placeholder=""/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer4 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input  type="text" defaultValue={teacher.exams[numberOfExam][i].answer4} placeholder=""/></li>
                                </ul>
                            )
                        })
                }
                }else if(typeOfExam === 'T/F'){
                    for(let i=0;i<teacher.exams[numberOfQuestions].length;i++){
           
                        exams.push({
                            number:i,value:(
                                <ul className="admin-test" key={i}>
                                    <h1 className="available-exam">  {i+1}</h1>
                                    <li><input type="text"readOnly defaultValue={teacher.exams[numberOfExam][i].question}placeholder=" "/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer1 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input  type="text" readOnly defaultValue={teacher.exams[numberOfExam][i].answer1} placeholder=""/></li>
                                    <li ><i className={teacher.answers[numberOfExam][i].value === teacher.exams[numberOfExam][i].answer2 ? 'fas fa-circle':'fas fa-circle-notch' }></i>
                                    <input  type="text" readOnly defaultValue={teacher.exams[numberOfExam][i].answer2} placeholder=""/></li>
                                </ul>
                            )
                        })
                }
                }
        
            return exams.map((e)=>{return e.value})
        }
           



        return(
            back()
        )
    }
}

export default ExamView