import React, { Component } from "react";
import './Sent.css'
import 'axios'
import axios from "axios";

class Sent extends Component{

    state = {
        task:[],
        students:[],
        teacher:[]
    }

    
    addDegree(number1,number2){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let students = this.state.students;
       let  degree = +this.state.teacher.taskDegree[number1];
        students[number2].tasks[number1].stuDegree = degree;
        let stuName = students[number2].name;
        let stuTask = students[number2].tasks;
        // add degree of the task 
        axios({
            method:'post',
            url:'/home/updatetask',
            data:{
                student:stuName,task:stuTask
            }
        }).then(()=>{
            let name = JSON.parse(localStorage.getItem('teacher')).name
        
            axios({
                method:'post',
                url:'/home/recievetasks',
                data:{
                    name:name
                }
            }).then((res1)=>{
            this.setState({students:res1.data.students})

                // res1 == all students
                let name = JSON.parse(localStorage.getItem('teacher')).name
                axios({
                    method:'post',
                    url:'/home/recievealltasks',
                    data:{
                        name:name
                    }
                }).then(()=>{
                    document.body.style.opacity = 1
                    document.getElementById('loader').style.display = 'none'
                    this.refresh()
                })
            })
        })
   }

   zeroDegree(number1,number2){
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
    let students = this.state.students;
    students[number2].tasks[number1].stuDegree = 0;
    let stuName = students[number2].name;
    let stuTask = students[number2].tasks;
    // add degree of the task 
    axios({
        method:'post',
        url:'/home/updatetask',
        data:{
            student:stuName,task:stuTask
        }
    }).then(()=>{
        let name = JSON.parse(localStorage.getItem('teacher')).name
    
        axios({
            method:'post',
            url:'/home/recievetasks',
            data:{
                name:name
            }
        }).then((res1)=>{
        this.setState({students:res1.data.students})

            // res1 == all students
            let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/recievealltasks',
                data:{
                    name:name
                }
            }).then(()=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
                this.refresh()
            })
        })
    })
}


    componentDidMount(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let name = JSON.parse(localStorage.getItem('teacher')).name
        
        axios({
            method:'post',
            url:'/home/recievetasks',
            data:{
                name:name
            }
        }).then((res1)=>{
   
            // res1 == all students
            this.setState({students:res1.data.students})
            let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/recievealltasks',
                data:{
                    name:name
                }
            }).then((res2)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
            let task = []

                this.setState({teacher:res2.data.teacher})
            // res2 == teacher only
   
                let teacher = res2.data.teacher;
                let students = res1.data.students;

                for(let i=0;i<teacher.taskAddedName.length;i++){
                   for(let j =0;j<students.length;j++){
                        if(students[j].tasks[i] &&students[j].tasks[i].location !==null && students[j].tasks[i].stuDegree == null){
                            task.push({value:(  
                                <>
                                <ul className="sent2">
                                    <li>{students[j].name}</li>
                                    <li>{students[j].tasks[i].taskName}</li>
                                    <li><a download href={"/upload/"+students[j].tasks[i].location}><button onClick={()=>{

                                        }}></button></a></li>
                                    <li><i className="fas fa-check-circle" onClick={()=>{this.addDegree(i,j)}}></i></li>
                                    <li><i className="far fa-times-circle" onClick={()=>{this.zeroDegree(i,j)}} ></i></li>
                                </ul>  
                                </>
                                    )})
                        }
                       
                       this.setState({task:task})
                    
                   }
                }
            })
        })
    }

    refresh(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let name = JSON.parse(localStorage.getItem('teacher')).name
        
        axios({
            method:'post',
            url:'/home/recievetasks',
            data:{
                name:name
            }
        }).then((res1)=>{
            // res1 == all students
            this.setState({students:res1.data.students})
            let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/recievealltasks',
                data:{
                    name:name
                }
            }).then((res2)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
            let task = []

                this.setState({teacher:res2.data.teacher})
            // res2 == teacher only
   
                let teacher = res2.data.teacher;
                let students = res1.data.students;

                for(let i=0;i<teacher.taskAddedName.length;i++){
                   for(let j =0;j<students.length;j++){
                        if(students[j].tasks[i] &&students[j].tasks[i].location !==null && students[j].tasks[i].stuDegree == null){
                            task.push({value:(  
                                <>
                                <ul className="sent2">
                                    <li>{students[j].name}</li>
                                    <li>{students[j].tasks[i].taskName}</li>
                                    <li><a download href={"/upload/"+students[j].tasks[i].location}><button onClick={()=>{

                                        }}></button></a></li>
                                    <li><i className="fas fa-check-circle" onClick={()=>{this.addDegree(i,j)}}></i></li>
                                    <li><i className="far fa-times-circle"  onClick={()=>{this.zeroDegree(i,j)}}></i></li>
                                </ul>  
                                </>
                                    )})
                        }
                       
                       this.setState({task:task})
                    
                   }
                }
            })
        })
    }

render(){


  
    
    return(
        <div className="admin-sent">  
        {
            this.state.task.length>=1?
            (<ul className="sent">
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
        </ul>):(<p className="nothing2">   </p>)
        }
                {this.state.task.map((s)=>{return s.value})}
    </div>

    )
}
}

export default Sent