import React, {useState } from 'react'
import { useHistory} from 'react-router-dom'
import axios from 'axios'
import './Registration.css'
import Nav from '../../Nav/Nav'

function Registration (){

   
        let [name,setName] = useState('')
        let [email,setEmail] = useState('')
        let [password,setPassword] = useState('')
        let [confirm,setConfirm] = useState('')
        let [msg,setMSg] = useState('')
        let [msg2,setMSg2] = useState('')
        let history = useHistory()
        let submit = async()=>{
        
            let arr = name.split(' ');
            let newName  = '';
            for(let i =0;i<arr.length;i++){
                if(arr[i] !== ''){
                    newName += arr[i]
                    newName += ' '
                }
            }
            
        
                if(password === ""){
                    setMSg("   ")
                }else if( confirm === password ){
                    document.body.style.opacity = 0.9
                    document.getElementById('loader').style.display = 'flex'
                 
                    axios({
                        method:'post',
                        url:'/home/registration',
                        data:{
                            name:newName,
                            email:email,
                            password:password
                        }
                    }).then((res)=>{
                        document.body.style.opacity = 1
                        document.getElementById('loader').style.display = 'none'
                        if(res.data === false){
                            setMSg('  ')
                        }else{
                            setMSg2('  ')
                        }
                    })
                }else{
                    setMSg("   ")
                }      
            }
        
            let login = ()=>{
                history.push('/login')
            }


        return (
            <>
            <div class="lds-facebook" id="loader"><div></div><div></div><div></div></div>
                <Nav />
                <div className="registration">

            <div className="registration-div">
 
            </div>

                    <form className='rp'>
             
                    <span className="stu-tech"> 
                    </span>

        

                    <input onChange={(e)=>{setName(e.target.value)}} value={name} type="text" placeholder="  (  )" />
                    <input onChange={(e)=>{setEmail(e.target.value)}} value={email} type="email" placeholder=" " />
                    <input onChange={(e)=>{setPassword(e.target.value)}} value={password} type="password" placeholder=" " />
                    <input onChange={(e)=>{setConfirm(e.target.value)}} value={confirm} type="password" placeholder="  " />
                    <button onClick={(e)=>{e.preventDefault();submit()}}></button> 
                    <h5><a onClick={()=>{login()}}>     </a></h5>
                    <p className={msg ? 'nothing2 ': 
                    msg2 ? 'thing':''
                }>{msg2 ? msg2:msg}</p>
                    </form>
                </div>
           <footer className='footer'>Copyright  2022</footer>
            </>
        )
}

export default Registration