import React ,{Component} from "react";
import './Degree.css'
import axios from 'axios'

class Degree extends Component{
  state = {
      student:null,
      teacher:null
  }

  
  showExam(){
      let timeStart = [];
      let deg = [];
      for(let i=0;i<this.state.student.timeStart.length;i++)
      {
          timeStart.push(this.state.student.timeStart[i].exam);
      }
      for(let i=0;i<this.state.student.degrees.length;i++)
      {
          deg.push(this.state.student.degrees[i].name);
      }
      let arr = [];
      let degrees = this.state.student.degrees;
      for(let i=0;i<this.state.teacher.examsNames.length;i++){
          let indexStart = timeStart.indexOf(this.state.teacher.examsNames[i]);
          let indexDeg = deg.indexOf(this.state.teacher.examsNames[i]);
        if(indexDeg !== -1 && indexStart !== -1 && this.state.teacher.examsCase[i] === true){
            arr.push({value:(
                (
                <React.Fragment key={i}>
                        <ul className="stu-degree-sub">
                            <li>{this.state.teacher.examsNames[i]}</li>
                            <li>{degrees[indexDeg].degree}</li>
                        </ul>
                    </React.Fragment >
                )
            )})
        }else if(indexDeg === -1 && indexStart !== -1 && this.state.teacher.examsCase[i] === true){
            arr.push({value:(
                (
                <React.Fragment key={i}>
                        <ul className="stu-degree-sub">
                            <li>{this.state.teacher.examsNames[i]}</li>
                            <li>{0}</li>
                        </ul>
                    </React.Fragment >
                )
            )})
        }
        else if(indexDeg === -1 && indexStart === -1 && this.state.teacher.examsCase[i] === true){
            arr.push({value:(
                (
                <React.Fragment key={i}>
                        <ul className="stu-degree-sub">
                            <li>{this.state.teacher.examsNames[i]}</li>
                            <li>{0}</li>
                        </ul>
                    </React.Fragment >
                )
            )})
        }
      }
      return arr
  }


  showTasks(){
    let arr = []
    let tasks = this.state.student.tasks;
    for(let i=0;i<tasks.length;i++){
      arr.push({value:(
          (
          <React.Fragment key={i}>
                  <ul className="stu-degree-sub">
                      <li>{tasks[i].taskName}</li>
                      <li>{tasks[i].stuDegree == null ?( (new Date(tasks[i].taskLastTime).getTime()/1000/60 ) - (new Date().getTime()/1000/60) )> 0 ? "  ":"0":tasks[i].stuDegree}</li>
                  </ul>
              </React.Fragment >
          )
      )})
    }
    return arr
}
    componentDidMount(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let studentName = JSON.parse(localStorage.getItem('student')).name;
        let teacher = JSON.parse(localStorage.getItem('student')).teacher
        axios({
            method:'post',
            url:'/home/getexamanddegree',
            data:{
                name:studentName,teacher:teacher+" "
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
            this.setState({student:res.data.students,teacher:res.data.teacher})
        })
    }
    
    render(){
        
        return(
            <>


            {
           this.state.student ? 
           
           (
               <>
               <h2 className="lesson-added">  :</h2>   
              <div className="stu-degree">
              {
                  this.state.student.degrees.length >= 1 ? 
                  (
                  <><ul className="stu-degree-main">
                  <li></li>
                  <li></li>
                  

              </ul>
              { this.showExam().map((s)=>{return s.value})}
              </>
              ):(<p className="nothing2">   </p>)
              }
              </div>
            </>
           )
                  :
           (<p className="nothing2">   </p>)
         
         }



        {
            this.state.student ? 
            
            (
                <>
               <h2 className="lesson-added">  :</h2>
               <div className="stu-degree">
              {
                  this.state.student.tasks.length >= 1 ? 
                  (
                    <ul className="stu-degree-main">
                    <li></li>
                    <li></li>
                </ul> 
                  ):
            (<p className="nothing2">   </p>)

              }   
                   { this.showTasks().map((s)=>{return s.value})}
               </div>
               </>
            )
                   :
            (<p className="nothing2">   </p>)
         }


            </>
        )
    }
}

export default Degree