import React ,{useState,useEffect} from "react";
import axios from 'axios'
import './Degree.css'

function Degree(){

   let getStudent =async ()=>{
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
    let Authorization = localStorage.getItem('Authorization-teacher')
       await axios({
            method:'post',
            url:'/home/getstudent',
            headers:{
                Authorization:Authorization
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
            setTeacher(res.data.teacher)
           let accepted = []
           let notAccepted = []
            if(res.data.student){
                for(let i=0;i<res.data.student.length;i++){
                    
                    if(res.data.student[i].accept === true){
                       
                            accepted.push(res.data.student[i])
                        
                    }else{
                        notAccepted.push(res.data.student[i])
                    }
                   }
                    setAccepted(accepted)
                    setNotAccepted(notAccepted)
            }
        })
    }
    let [teacher,setTeacher] = useState('')
    let [accepted,setAccepted] = useState([])
    let [notAccepted,setNotAccepted] = useState([])
    let [name,setName] = useState('');
    let [email,setEmail] = useState('');
    let [password,setPassword] = useState('');


    let addStudent =async ()=>{
        if(name.length >=4 && email.length >=4 &&password.length >=4){
            document.body.style.opacity = 0.9
            document.getElementById('loader').style.display = 'flex'
            let teacherName = JSON.parse(localStorage.getItem('teacher')).name
            let arr = teacherName.split(' ');
            let newName  = '';
            for(let i =0;i<arr.length;i++){
                if(arr[i] !== ''){
                    newName += arr[i]
                    newName += ' '
                }
            }
            
          await  axios({
                    method:'post',
                    url:'/home/registration',
                    data:{
                            teacher:newName.slice(0,newName.length-1),
                            name:name,
                            email:email,
                            password:password
                        }
                    }).then(()=>{
                        document.body.style.opacity = 1
                        document.getElementById('loader').style.display = 'none'
                        setName('')
                        setPassword('')
                        setEmail('')
                        getStudent()
                    })
            
        }
    }



    let  agree = async(i)=>{
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let taskArr = [];
        for(let i=0;i<teacher.taskAddedName.length;i++){
            taskArr.push({taskName:teacher.taskAddedName[i],taskDegree:teacher.taskDegree[i],taskDetail:teacher.taskAdded[i],taskLastTime:teacher.taskLastTime[i],location:null,stuDegree:null})
        }
       await axios({
            method:'post',
            url:'/home/accept',
            data:{
                name:notAccepted[i].name,
                taskArr:taskArr
            }
        }).then(()=>{
            let Authorization = localStorage.getItem('Authorization-teacher')
            axios({
                method:'post',
                url:'/home/getstudent',
                headers:{
                    Authorization:Authorization
                }
            }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
               let accepted = []
               let notAccepted = []
               if(res.data.student){
                for(let i=0;i<res.data.student.length;i++){
                    if(res.data.student[i].accept === true){
                        accepted.push(res.data.student[i])
                    }else{
                        notAccepted.push(res.data.student[i])
                    }
                   }
               }
                setAccepted(accepted)
                setNotAccepted(notAccepted)
                
            })
         


        })
    }

    let disAgree = async(i)=>{
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let taskArr = [];
        
       

        for(let i=0;i<teacher.taskAddedName.length;i++){
            taskArr.push({taskName:teacher.taskAddedName[i],taskDegree:teacher.taskDegree[i],taskDetail:teacher.taskAdded[i],taskLastTime:teacher.taskLastTime[i],location:null,stuDegree:0})
        }
       await axios({
            method:'post',
            url:'/home/disagree',
            data:{
                name:notAccepted[i].name
            }
        }).then(()=>{
            let Authorization = localStorage.getItem('Authorization-teacher')
            axios({
                method:'post',
                url:'/home/getstudent',
                headers:{
                    Authorization:Authorization
                }
            }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
               let accepted = []
               let notAccepted = []
               if(res.data.student){
                for(let i=0;i<res.data.student.length;i++){
                    if(res.data.student[i].accept === true){
                        accepted.push(res.data.student[i])
                    }else{
                        notAccepted.push(res.data.student[i])
                    }
                   }
               }
                setAccepted(accepted)
                setNotAccepted(notAccepted)
                
            })
         


        })
    }

    useEffect(()=>{
        getStudent()    
    },[])
    

  
    let printDegrees = (x) =>{
        let arrOrdered = [];
        let arrOrderedStart = [];
        for(let i=0;i<teacher.examsNames.length;i++){
            if(accepted[x].degrees[i]){   
                arrOrdered.push(accepted[x].degrees[i].name)
            }
            if(accepted[x].timeStart[i]){
                arrOrderedStart.push(accepted[x].timeStart[i].exam)
            }
        }
        
        let arr = [];
        for(let i=0;i<teacher.examsNames.length;i++){
           let index = arrOrdered.indexOf(teacher.examsNames[i]);
            let indexStart = arrOrderedStart.indexOf(teacher.examsNames[i]);
            if( arrOrdered.indexOf(teacher.examsNames[i]) !== -1){
               console.log(arrOrdered.indexOf(teacher.examsNames[i]))
                arr.push(
                   {value:( <>
                    <li>{accepted[x].degrees[index].degree}</li>
                    </>)}
                )
               
            }else if( indexStart !== -1){
               
                arr.push(
                    {value:( <>
                     <li>{" "}</li>
                     </>)}
                 )
            }else{
                arr.push(
                    {value:(<>
                        <li>{" "}</li>
                        </>)}
                )
            }
        }
        return arr
    }

    let printTasks = (x) =>{
        let arr = [];
        for(let i=0;i<teacher.taskAddedName.length;i++){
            if(accepted[x].tasks[i].stuDegree != null){
                arr.push({value:(
                    <>
                    <li>{accepted[x].tasks[i].stuDegree}</li>
                    </>
                )})
            }else{
                arr.push(
                   {value:( <>
                    <li>{" "}</li>
                    </>)}
                )
            }
        }
        return arr
    }

       
    
    let nameEmail = ()=>{
        let arr = [];
        for(let i=0;i<accepted.length;i++){
            arr.push({value:(
                <React.Fragment key={i}>
            <ul className="admin-degree-sub3">
                <li>{accepted[i].name}</li>
                <li>{accepted[i].email}</li>

            {
             printDegrees(i).map((s)=>{
                return s.value
            })
            }

            {
                printTasks(i).map((s)=>{
                    return s.value
                })   
            }

            </ul>
            </React.Fragment>
            )})
        }
        return arr
    }



    let NotAcceptedStudent = ()=>{
        let arr = [];
       for(let i=0;i<notAccepted.length;i++){
        arr.push({value: (
            <ul className="admin-degree-sub3" key={Math.random()}>
                <li>{notAccepted[i].name}</li>
                <li><i className="fas fa-check-circle" onClick={()=>{
                    agree(i)
                }}></i></li>
                 <li><i className="far fa-times-circle" onClick={()=>{
                     disAgree(i)
                 }}></i></li>
            </ul>
            )})
       }
       
       return arr.map((s)=>{
        return s.value
    })
    }

    return(
        <>
            <div className="admin-degree-last">
            <h3> </h3>
            <div className="fix-main3">

            {
            (
                <>
                <ul className="testo">
                <li></li>
                <li></li>
                <li></li>
            </ul>
                {
               NotAcceptedStudent()
                }
            </>
            )}
            </div>
            </div>


            <div className="admin-degree-last2">
            <h3> </h3>
            <div className="fix-main3">
            {
            (
                <>
                <ul className="testo">
                <li> </li>
                <li></li>
                <li> </li>
            </ul>
            <ul className="testo2">
                <li><input type="text" value={name} placeholder="  " onChange={(e)=>{setName(e.target.value)}}/></li>
                <li><input type="text"value={email} placeholder=" " onChange={(e)=>{setEmail(e.target.value)}} /></li>
                <li><input type="password" value={password}placeholder="  " onChange={(e)=>{setPassword(e.target.value)}}/></li>
            </ul>
            </>
            )}
            </div>
            </div>
            <div className='div-apload'>
            <button className="add-stu" onClick={()=>{addStudent()}}></button>
             </div>
           






            <div className="admin-degree3">
                <h1>  </h1>
                <div className="fix-main3">
                 <ul className="admin-degree-main3">
                     <li></li>
                     <li> </li>
                     {
                         teacher ? 
                         teacher.examsNames.map((n)=>{
                            return (
                               <li>{n}</li>
                            )
                        }):null
                     }
                      {
                         teacher ? 
                         teacher.taskAddedName.map((n)=>{
                            return (
                               <li>{n}</li>
                            )
                        }):null
                     }
                 </ul>

                {
                   
                        (
                           <React.Fragment  key={Math.random()}>
                       {
                           nameEmail().map((s)=>{
                               return s.value
                           })
                       }

                       

                        </React.Fragment>
                        )
                   
                }

             </div>




             </div>
        </>
    )
}


export default Degree