import React , {useState,useEffect} from 'react'
import './Lecture.css'
import axios from 'axios'
function Lecture(){



    let [lecture,setLecture] = useState(null)

    let [links,setLinks] = useState(null)
    let [video,setVideo] = useState(0)
    let [fileName,setFileName] = useState(null)
    let [lessonName,setLessonName] = useState(null)
    let [err,setErr] = useState('')

    useEffect(()=>{
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let teacher = JSON.parse(localStorage.getItem('teacher'))
        axios({
            method:'post',
            url:'/home/getLecture',
            data:{
                teacher:teacher.name
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
            setLinks(res.data.lessons)
            setLessonName(res.data.fileName)

            show()
        })
    },[])


    let did = ()=>{
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let teacher = JSON.parse(localStorage.getItem('teacher'))
        axios({
            method:'post',
            url:'/home/getLecture',
            data:{
                teacher:teacher.name
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
            setLinks(res.data.lessons)
            setLessonName(res.data.fileName)

            show()
        })
    }
    let show = ()=>{
        let arr = []
           if(links !== null && lessonName !== null){
               console.log(lessonName)
            for(let i=0;i<links.length;i++){
              
                arr.push(
                    <div key={i} onClick={()=>{setVideo(i)}} className="video">{lessonName[i]}</div>
                )
           }
            }
            return arr

    }


    let handler = async()=>{ 
            if(!lecture || !fileName){
                setErr('   ')
            }else{
                let str ;
                let stop = false;
                let arrStr = [];
                for(let i=0;i<lecture.name.length;i++){
                    if(stop === true){
                        arrStr.push(lecture.name[i])
                    }
                    if(lecture.name[i] === '.'){
                        stop = true;
                    }
                }
                if(arrStr.length === 3){
                    str = ""+arrStr[0] +""+arrStr[1] +""+arrStr[2] 
                }
                
                const fileTypes = /mp4|mov|wmv|avi|avchd|flv|f4v|swf|mkv|webm|mpeg-2/;
                const mimetype = fileTypes.test(str);
                if (mimetype) {
                  setErr("");
                }else{
                  setErr("   ");
                }

                if(!err){
                    document.body.style.opacity = 0.9
                    document.getElementById('loader').style.display = 'flex'
                    let formDate = new FormData();
                    formDate.append('lecture',lecture)
                    formDate.append('name',fileName)
                    let email = JSON.parse(localStorage.getItem('teacher')).email
                await    axios({
                        method:'post', 
                        url:'/home/addlecture',
                        data:formDate,
                        headers:{
                            data:email
                        }
                    }).then(async()=>{
                        document.body.style.opacity = 1
                        document.getElementById('loader').style.display = 'none'
                        setLecture(null)
                        
                        did()
                    })
                     
                }
        
         
            }
        }





    
    return(
        <>
            <div className="admin-task">
              <div className="admin-task2">
              <p></p>
              <p className="admin-task2-p"> </p>
              </div>


              <div className="admin-task3">
              <p><input type="text" placeholder=" " value={fileName} onChange={(e)=>{setFileName(e.target.value)}}/></p>
              <input  onChange={(e)=>{setLecture(e.target.files[0])}} type="file"/>
              </div>
              </div>
             <div className='div-apload'>
             <button className="admin-apload" onClick={()=>{handler()}}></button>
             </div>
            {err ? <p className="nothing2">{err}</p>:null}
       {
           
           links !== null ? (
               <>
               <h2 className="lesson-added">  :</h2>
               <div className="admin-lecture">
               {   
                show()
               }
               </div>
            <div  className="video">
            {
                   links ?   links[video] ? (<video
                    id="my-video"
                    className="video-js"
                    controls
                    preload="auto"
                    poster="MY_VIDEO_POSTER.jpg"
                    data-setup="{}"
                    src={`/home/upload/${links[video]}`}
                  >
                  </video>):null
                  :null
               }

            </div>
  
               </>
           ):(
            <p className="nothing2">   </p>
           )
       }
        </>
    )
}

export default Lecture