    import React , {Component} from 'react'
    import axios from 'axios'
    import Test from '../Test/Test'
    import './Exam.css'

    class Exam extends Component{

    state  = {
    preExams:[],
    teacher:[],
    test:false,
    numberOfI:'',
    months : ["","","","","","","","","","","",""],
    date:'',
    stuName:'',
    time:'',
    start:true,
    typeOfExam:'',
    examName:'',
    timing:'',
    show:false,
    }


    which(){
    if(this.state.test === false){
    return(
    <>
    <h2 className="available-exam">  :</h2>
    {
    this.state.show?  

    <div className="admin-exam">
    <ul className="testo-exam">
    <li></li>
    <li></li>
    <li> </li>
    <li></li>
    </ul>
    {this.state.preExams.map((e)=>{
    return e.value
    })}
    </div>                 
    :
    (
    <>
    <p className="nothing2">   </p>
    </>
    )
    } 






    </>
    )
    }else{
    return <Test timing = {this.state.timing} examName={this.state.examName} examDegree = {+this.state.teacher.degree[this.state.numberOfI]} studentAnswers={[]} teacher={this.state.teacher} question = {this.state.teacher.exams[this.state.numberOfI]} typeOfExam={this.state.typeOfExam} date={this.state.time } start={this.state.start}  numberOfI={this.state.numberOfI}/>
    }        
    }
    
    componentDidMount(){
    document.body.style.opacity = 0.9
    document.getElementById('loader').style.display = 'flex'
    let teacher = JSON.parse(localStorage.getItem('student')).teacher;
    let name = JSON.parse(localStorage.getItem('student')).name;
    this.setState({stuName:name})
    axios({
    method:'post',
    url:'/home/getexams',
    data:{
    teacher:teacher+" ",name:name
    }
    }).then((res)=>{
    document.body.style.opacity = 1
    document.getElementById('loader').style.display = 'none'
    let show = false;
    let studentExam = res.data.studentExam  
    let arr = []; 
    this.setState({teacher:res.data.teacher})
    let stuexam = [];
    let stuDegrees = [];
    for(let i=0;i<studentExam.timeStart.length;i++){
    stuexam.push(studentExam.timeStart[i].exam)
    }
    for(let i=0;i<studentExam.degrees.length;i++){
    stuDegrees.push(studentExam.degrees[i].name)
    }
    // print prev Exam
    if(res.data.teacher !== null){
    for(let i=0;i<res.data.teacher.examsNames.length;i++){
    let index =  stuDegrees.indexOf(res.data.teacher.examsNames[i])
    let index2 =  stuexam.indexOf(res.data.teacher.examsNames[i])

    // let index2 =  stuexam.indexOf(res.data.teacher.examsNames[i])
    if(index=== -1){
    if((studentExam.timeStart[index2] && index2 !== -1))  {
    if(res.data.teacher.examsCase[i] === true  && (new Date(  studentExam.timeStart[index2].time).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0 && (new Date(  res.data.teacher.dateLastTime[i]).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0  ){
    show = true 


    arr.push({value: <>
    {res.data.teacher.examsCase[i] === true  ? (

    <div key={Math.random()} className="admin-exam">
    <ul className="testo2-exam">
    <li>{res.data.teacher.examsNames[i]}</li>
    <li>{res.data.teacher.time[i] + "  "}</li>
    <li>{` ${new Date(res.data.teacher.dateLastTime[i]).getSeconds() < 10 ?
    "0" + new Date(res.data.teacher.dateLastTime[i]).getSeconds():
    new Date(res.data.teacher.dateLastTime[i]).getSeconds()}:
    ${ new Date(res.data.teacher.dateLastTime[i]).getMinutes() < 10 ? 
    "0" +  new Date(res.data.teacher.dateLastTime[i]).getMinutes() :
    new Date(res.data.teacher.dateLastTime[i]).getMinutes()} :
    ${new Date(res.data.teacher.dateLastTime[i]).getHours() > 12 ? 
        new Date(res.data.teacher.dateLastTime[i]).getHours() > 10 ?
        new Date(res.data.teacher.dateLastTime[i]).getHours()-12
        + "    " :  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours()-12 + 
        "    ": new Date(res.data.teacher.dateLastTime[i]).getHours() > 10? 
        new Date(res.data.teacher.dateLastTime[i]).getHours() + "  ":  "0" 
        + new Date(res.data.teacher.dateLastTime[i]).getHours() + "    "  } 
            ,  ${new Date(res.data.teacher.dateLastTime[i]).getDate() } 
            ${this.state.months[new Date(res.data.teacher.dateLastTime[i]).getMonth()] }`}</li>
    <li>{res.data.teacher.degree[i]}</li>
    </ul>

    <div className="btn-2"><button onClick={()=>{ 
            
    if(res.data.teacher.exams[i][0].answer3){
        this.setState({typeOfExam:'MCQ'})
    
    }else{
   
        this.setState({typeOfExam:'T/F'})
    }
    this.setState({numberOfI:i,test:true}) ;
    let index = stuexam.indexOf(this.state.teacher.examsNames[i])

    
    if(index === -1 ){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        axios({
            method:'post',
            url:'/home/timestart',
            data:{
                examName:res.data.teacher.examsNames[i], name:this.state.stuName,
                time:new Date(new Date().getTime() + (res.data.teacher.time[i] *60*1000) )
                    }
        }).then(()=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
        })
        this.setState({time:this.state.teacher.time[i],timing:'',examName:this.state.teacher.examsNames[i]})
    }else{
        this.setState({start:false,time:this.state.teacher.time[i],timing: studentExam.timeStart[index].time,examName:this.state.teacher.examsNames[i]})
    }
        }}></button></div>

    </div>
    ) 
    :null} </>})
    }

    }else{
    if(res.data.teacher.examsCase[i] === true  && (new Date(  res.data.teacher.dateLastTime[i]).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0  ){
        show = true 
        

        arr.push({value: <>
            {res.data.teacher.examsCase[i] === true  ? (
                <>
                
                <ul className="testo2-exam">
                <li>{res.data.teacher.examsNames[i]}</li>
                <li>{res.data.teacher.time[i] + "  "}</li>
                <li>{` ${new Date(res.data.teacher.dateLastTime[i]).getSeconds() < 10 ?
                        "0" + new Date(res.data.teacher.dateLastTime[i]).getSeconds():
                        new Date(res.data.teacher.dateLastTime[i]).getSeconds()}:
                        ${ new Date(res.data.teacher.dateLastTime[i]).getMinutes() < 10 ? 
                        "0" +  new Date(res.data.teacher.dateLastTime[i]).getMinutes() :
                        new Date(res.data.teacher.dateLastTime[i]).getMinutes()} :
                        ${new Date(res.data.teacher.dateLastTime[i]).getHours() > 12 ? 
                            new Date(res.data.teacher.dateLastTime[i]).getHours() > 10 ?
                            new Date(res.data.teacher.dateLastTime[i]).getHours()-12
                            + "    " :  "0" + new Date(res.data.teacher.dateLastTime[i]).getHours()-12 + 
                            "    ": new Date(res.data.teacher.dateLastTime[i]).getHours() > 10? 
                            new Date(res.data.teacher.dateLastTime[i]).getHours() + "  ":  "0" 
                            + new Date(res.data.teacher.dateLastTime[i]).getHours() + "    "  } 
                                ,  ${new Date(res.data.teacher.dateLastTime[i]).getDate() } 
                                ${this.state.months[new Date(res.data.teacher.dateLastTime[i]).getMonth()] }`}</li>
                <li>{res.data.teacher.degree[i]}</li>
                </ul>
                    
                    <div className="btn-2"><button onClick={()=>{ 
                                
                        if(res.data.teacher.exams[i][0].answer3){
                            this.setState({typeOfExam:'MCQ'})
                          
                        }else{
                         

                            this.setState({typeOfExam:'T/F'})
                        }
                        this.setState({numberOfI:i,test:true}) ;
                        let index = stuexam.indexOf(this.state.teacher.examsNames[i])
                    
                        if(index === -1 ){
                            document.body.style.opacity = 0.9
                            document.getElementById('loader').style.display = 'flex'
                            axios({
                                method:'post',
                                url:'/home/timestart',
                                data:{
                                    examName:res.data.teacher.examsNames[i], name:this.state.stuName,
                                    time:new Date(new Date().getTime() + (res.data.teacher.time[i] *60*1000) )
                                        }
                            }).then(()=>{
                                document.body.style.opacity = 1
                                document.getElementById('loader').style.display = 'none'
                            })
                            this.setState({time:this.state.teacher.time[i],timing:'',examName:this.state.teacher.examsNames[i]})
                        }else{
                            this.setState({start:false,time:this.state.teacher.time[i],timing: studentExam.timeStart[index].time,examName:this.state.teacher.examsNames[i]})
                        }
                            }}></button></div>
                    
                    </>
            ) 
            :null} </>})
    }
    }

    }

    }
    this.setState({show:show,preExams:arr})
    }
    })
    }





    render(){
    return this.which()
    }

    }
    export default Exam