import React , {Component} from 'react'
import './Task.css'
import axios from 'axios'

let taskFile = [];
class Task extends Component{
    state = {
        task:[],
        taskName : [],
        taskLastTime:[],
        taskDegree:[],
        stuDegree:[],
        locations:[],
        err:'',
        months : ["","","","","","","","","","","",""],
        sh:false
    }
    
 

    
   async handler(name,detail,lastTime,x){

        
    if(!taskFile[x]){
        this.setState({err:'  '})
    }else{
        
        let str ;
        let stop = false;
        let arrStr = [];
        for(let i=taskFile[x].name.length-1;i>0;i--){
            if(taskFile[x].name[i] === '.'){
                stop = true;
                break;
            }
            if(stop === false){
                arrStr.push(taskFile[x].name[i])
            }
        }
        if(arrStr.length === 3){
            str = ""+arrStr[2] +""+arrStr[1] +""+arrStr[0] 
        }else if(arrStr.length === 4){
            str = ""+arrStr[3] +""+arrStr[2] +""+arrStr[1] +""+arrStr[0]  
        }
        
        
        const fileTypes = /pdf|rar/;
        const mimetype = fileTypes.test(str);
       
        if (mimetype) {
            document.body.style.opacity = 0.9
            document.getElementById('loader').style.display = 'flex'

            this.setState({student:this.state.student})
          this.setState({err:""});
          let formDate = new FormData();
          formDate.append('tasks',taskFile[x])
          let student = JSON.parse(localStorage.getItem('student')).name
          formDate.append('props',JSON.stringify(this.state.student.tasks))
          formDate.append('x',x)
          formDate.append('name',student)
        //   formDate.append('taskName',name)
        //   formDate.append('taskDetail',detail)
        //   formDate.append('taskLastTime',lastTime)
        //   formDate.append('degree',null)
           
           await    axios({
              method:'post',
              url:'/home/taskdegree',
              data:formDate,
          }).then(async()=>{
              this.setState({task:[]})
              for(let i =0;i<taskFile.length;i++){
                taskFile.pop()
              }
              let teacher = JSON.parse(localStorage.getItem('student')).teacher
              let student = JSON.parse(localStorage.getItem('student')).name
        await   axios({
                  method:'post',
                  url:'/home/showtask',
                  data:{
                      teacher:teacher,student:student
                  }
              }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
               // data comes from backend
               let student = res.data.student;
               let teacher = res.data.teacher;
               // array to filter information
               let taskName = [];
               let taskDetail = [];
               let taskLastTime = [];
               let taskDegree = [];
               let stuDegree = []
               let locations = []
               // put student task in array 
               for(let i=0;i<student.tasks.length;i++){
                if(student.tasks[i].stuDegree === null && student.tasks[i].location === null && (new Date(student.tasks[i].taskLastTime).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0 )
                {console.log(true);this.setState({sh:true})}
                taskName.push(student.tasks[i].taskName)
                taskDetail.push(student.tasks[i].taskDetail)
                taskLastTime.push(student.tasks[i].taskLastTime)
                taskDegree.push(student.tasks[i].taskDegree)
                stuDegree.push(student.tasks[i].stuDegree)
                locations.push(student.tasks[i].location)

            }
       
            this.setState({
                locations:locations, student:student,teacher:teacher, taskName:taskName,taskDegree:taskDegree,taskDetail:taskDetail,taskLastTime:taskLastTime,stuDegree:stuDegree
            })
   
              })
          })

        }else{
            
          this.setState({err: `     5     pdf , rar    ` });
        }


      
    }
    }
    componentDidMount(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let teacher = JSON.parse(localStorage.getItem('student')).teacher
        // let teacher = teacher1.substring(0, teacher1.length - 1)
        let student = JSON.parse(localStorage.getItem('student')).name
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        axios({
            method:'post',
            url:'/home/showtask',
            data:{
                teacher:teacher,student:student
            }
        }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
            // data comes from backend
            let student = res.data.student;
            let teacher = res.data.teacher;
            // array to filter information
            let taskName = [];
            let taskDetail = [];
            let taskLastTime = [];
            let taskDegree = [];
            let stuDegree = []
            let locations = []
            // put student task in array 
         
            for(let i=0;i<student.tasks.length;i++){
                if(student.tasks[i].stuDegree === null && student.tasks[i].location === null && (new Date(student.tasks[i].taskLastTime).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0 )
                {console.log(true);this.setState({sh:true})}
                taskName.push(student.tasks[i].taskName)
                taskDetail.push(student.tasks[i].taskDetail)
                taskLastTime.push(student.tasks[i].taskLastTime)
                taskDegree.push(student.tasks[i].taskDegree)
                stuDegree.push(student.tasks[i].stuDegree)
                locations.push(student.tasks[i].location)

            }
       
            this.setState({
                locations:locations, student:student,teacher:teacher, taskName:taskName,taskDegree:taskDegree,taskDetail:taskDetail,taskLastTime:taskLastTime,stuDegree:stuDegree
            })

            
    }
        )}

        show(){
         
            let show = [];
           if(this.state.taskName.length >= 1 ){
            for(let i=0;i<this.state.taskName.length;i++){
            
                if(this.state.stuDegree[i] === null && this.state.locations[i] === null && (new Date(this.state.taskLastTime[i]).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0 ){
           
                    show.push({value:
              (
                  <React.Fragment key={i}>
                  <div className="stu-task3">
                  <p>{this.state.taskName[i]}</p>    
                  <p>{this.state.taskDetail[i]}</p>
                  <p>{this.state.taskDegree[i]}</p>
                  <p>{` ${new Date(this.state.taskLastTime[i]).getSeconds() < 10 ? "0" + new Date(this.state.taskLastTime[i]).getSeconds():new Date(this.state.taskLastTime[i]).getSeconds()}: ${ new Date(this.state.taskLastTime[i]).getMinutes() < 10 ? "0" +  new Date(this.state.taskLastTime[i]).getMinutes() : new Date(this.state.taskLastTime[i]).getMinutes()} : ${new Date(this.state.taskLastTime[i]).getHours() > 12 ?  new Date(this.state.taskLastTime[i]).getHours() > 10 ? new Date(this.state.taskLastTime[i]).getHours()-12 + "    " :  "0" + new Date(this.state.taskLastTime[i]).getHours()-12 + "    ": new Date(this.state.taskLastTime[i]).getHours() > 10?  new Date(this.state.taskLastTime[i]).getHours() + "  ":  "0" + new Date(this.state.taskLastTime[i]).getHours() + "    "  }  , ${new Date(this.state.taskLastTime[i]).getDate()} ${this.state.months[new Date(this.state.taskLastTime[i]).getMonth()]} `}</p>
    
                  <input onChange={(e)=>{ taskFile[i] = e.target.files[0] }}   type="file"/>
                  </div>
            <div className='div-apload'> 
                  <button className="stu-apload" onClick={()=>{this.handler(this.state.taskName[i],this.state.taskDetail[i],this.state.taskLastTime[i],i)}}></button>
                 </div>
                  </React.Fragment>
              )}) 
                }
            }
            return show ;
           }
        }


   render(){
    return(
        <>
      {
        this.state.sh   ? 
          (
              <>
              <div className="stu-task">
            
                 <div className="stu-task2">
                <p></p>
                <p></p>
                <p></p>
                <p> </p>
                <p className="stu-task2-p"> </p>
                </div>
            

            {
                 this.state.taskName.length >= 1 ? 
                 this.show().map((s)=>{
                    return s.value
                   }):null
            }
  
              </div>
              {this.state.err ? <p className="nothing2">{this.state.err}</p>:null}
</>
          ):( <>

          <p className="nothing2">  </p></>)
      }
        </>
    )
   }
}

export default Task