import React , {Component}from 'react'
import './Main.css'
import axios from 'axios'

class Main extends Component{

    state = {
        teacher:'',
        best:[],
        fileName:'',
        exam:'',
        examTime:[1],
        examDegree:[1],
        task:'',
        taskTime:'',
        taskDegree:'',
        request:'',
      
    }

    
    remove(name){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
    
        
    
        axios({
            method:'post',
            url:'/home/disagree',
            data:{
                name:name
            }
        }).then(()=>{
            let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/adminhome',
                data:{
                    name:name
                }
            }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
                this.setState({
                    best:res.data.best,
                    exam:res.data.teacher[0].examsNames[res.data.teacher[0].examsNames.length-1],
                    examTime:res.data.teacher[0].time[res.data.teacher[0].time.length-1],
                    examDegree:res.data.teacher[0].degree[res.data.teacher[0].degree.length-1],
                    fileName:res.data.teacher[0].fileName[res.data.teacher[0].fileName.length-1],
                    task:res.data.teacher[0].taskAdded[res.data.teacher[0].taskAdded.length-1],
                    taskTime:res.data.teacher[0].taskLastTime[res.data.teacher[0].taskLastTime.length-1],
                    taskDegree:res.data.teacher[0].taskDegree[res.data.teacher[0].taskDegree.length-1],
                    
                })
            
            })
        })
    }

    add(name){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let taskArr = [];
        
        for(let i=0;i<this.state.teacher.taskAddedName.length;i++){
            taskArr.push({taskName:this.state.teacher.taskAddedName[i],taskDegree:this.state.teacher.taskDegree[i],taskDetail:this.state.teacher.taskAdded[i],taskLastTime:this.state.teacher.taskLastTime[i],location:null,stuDegree:null})
        }

        axios({
            method:'post',
            url:'/home/accept',
            data:{
                name:name,
                taskArr:taskArr
            }
        }).then(()=>{
            let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/adminhome',
                data:{
                    name:name
                }
            }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
                this.setState({
                    best:res.data.best,
                    exam:res.data.teacher[0].examsNames[res.data.teacher[0].examsNames.length-1],
                    examTime:res.data.teacher[0].time[res.data.teacher[0].time.length-1],
                    examDegree:res.data.teacher[0].degree[res.data.teacher[0].degree.length-1],
                    fileName:res.data.teacher[0].fileName[res.data.teacher[0].fileName.length-1],
                    task:res.data.teacher[0].taskAdded[res.data.teacher[0].taskAdded.length-1],
                    taskTime:res.data.teacher[0].taskLastTime[res.data.teacher[0].taskLastTime.length-1],
                    taskDegree:res.data.teacher[0].taskDegree[res.data.teacher[0].taskDegree.length-1],
                    
                })
            
            })
        })
    }
    componentDidMount(){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
        let name = JSON.parse(localStorage.getItem('teacher')).name
            axios({
                method:'post',
                url:'/home/adminhome',
                data:{
                    name:name
                }
            }).then((res)=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
                this.setState({
                    teacher:res.data.teacher[0],
                    best:res.data.best,
                    exam:res.data.teacher[0].examsNames[res.data.teacher[0].examsNames.length-1],
                    examTime:res.data.teacher[0].time[res.data.teacher[0].time.length-1],
                    examDegree:res.data.teacher[0].degree[res.data.teacher[0].degree.length-1],
                    fileName:res.data.teacher[0].fileName[res.data.teacher[0].fileName.length-1],
                    task:res.data.teacher[0].taskAddedName[res.data.teacher[0].taskAddedName.length-1],
                    taskTime:res.data.teacher[0].taskLastTime[res.data.teacher[0].taskLastTime.length-1],
                    taskDegree:res.data.teacher[0].taskDegree[res.data.teacher[0].taskDegree.length-1],
                })
            })
    }

        render(){

            let printReq = ()=>{
                let arr = [];
                for(let i=0;i<this.state.best[3].length ;i++){
                    arr.push({value:( 
                        <ul className="testo2">
                            <li>{this.state.best[3][i]}</li>
                            <li><i onClick={()=>{this.add(this.state.best[3][i])}} className="fas fa-check-circle"></i></li>
                            <li><i onClick={()=>{this.remove(this.state.best[3][i])}} className="far fa-times-circle"></i></li>
                        </ul>
                                )})
                            }
                            return arr.map((s)=>{
                                return s.value
                            })
            }

   

    return(
        <>
    <div className="admin-main">

    <div className="admin-main-first">
            <h3> </h3>
            

            {
                this.state.best[0] ? 
            this.state.best[0].name   ? (
                <>
                <ul className="testo">
                <li></li>
                <li></li>
                <li> </li>
            </ul>
            <ul className="testo2">
                <li>1#</li>
                <li>{this.state.best[0].name === "" ? " ": this.state.best[0].name}</li>
                <li>{this.state.best[0].degree === "" ? " ": this.state.best[0].degree}</li>
            </ul>
            <ul className="testo2">
                <li>2#</li>
                <li>{this.state.best[1].name === "" ? " ": this.state.best[1].name}</li>
                <li>{this.state.best[1].degree === "" ? " ": this.state.best[1].degree}</li>
            </ul>
            <ul className="testo2">
                <li>3#</li>
                <li>{this.state.best[2].name === "" ? " ": this.state.best[2].name}</li>
                <li>{this.state.best[2].degree === "" ? " ": this.state.best[2].degree}</li>
            </ul>
            </>
            ):(
                <>
                <p className="nothing"> </p>
                </>
            )
            :(
                <>
                <p className="nothing"> </p>
                </>
            )
        }
        </div>
        


   
        <div className="admin-main-last">
            <h3> </h3>
            {
           this.state.best ? 
           this.state.best.length >= 1 ?
           this.state.best[3].length >= 1 ? 
           (
               <>
               <ul className="testo">
               <li></li>
               <li></li>
               <li></li>
           </ul>
           {
             [...printReq()]
           }
           </>
           ):(
               <>
               <p className="nothing"> </p>
               </>
           ) 
       :(
           <>
           <p className="nothing"> </p>
           </>
       ):<>
       <p className="nothing"> </p>
       </>
        }
        </div>

    </div>
        </>
    )
        }
}

export default Main