import React, { useState,useRef} from 'react'
import { useHistory} from 'react-router-dom'
import './Login.css'
import Nav from '../../Nav/Nav'
import axios from 'axios'

function Login (){
   
    const [emailValue,setEmailValue] = useState()
    const [passwordValue,setPasswordValue] = useState()
    const [err,setErr] = useState(null)
    const email = useRef()
    const password = useRef()
    let history = useHistory()

    


    let submit = async()=>{
        if(passwordValue&&emailValue){
            document.body.style.opacity = 0.9
            document.getElementById('loader').style.display = 'flex'
            await axios({
                method:'post',
                url:'/home/login',
                data:{
                    email:emailValue,password:passwordValue
                }
            }).then((res)=>{
            document.body.style.opacity = 1
            document.getElementById('loader').style.display = 'none'
                if(res.data.student === "false"){
                    setErr("  ")
                }
                else if(res.data.token){
                    if(res.data.student){
                        localStorage.setItem("Authorization-student",res.data.token)
                        localStorage.setItem("student",JSON.stringify(res.data.student))
                    }
                    else if(res.data.teacher){
                        localStorage.setItem("Authorization-teacher",res.data.token)
                        localStorage.setItem("teacher",JSON.stringify(res.data.teacher))
                    }
                

                    handelHestory(res.data.kind)
                }
                else{
                    if(res.data.err){
                        setErr(res.data.err)
                    }
                    email.current.value = ''
                    password.current.value = ''
                }
            })
        }else{
            setErr('   ')
        }
    }

    let handelHestory = (kind)=>{
        if(kind === 'student'){
            history.push('/student')
           }else{
            history.push('/admin')
           }
    }

    let registeration = ()=>{
        history.push('/registration')
    }

        return (
            <>
                 <div class="lds-facebook" id="loader"><div></div><div></div><div></div></div>

                <Nav />
                <div className="login">

            <div className="login-div">
            </div>
                    <form>
            <div>
                        {err ? (<p className="login-err">{err}</p>):<p className="login-normal"></p>}
                    <input ref={email} onChange={(e)=>{setEmailValue(e.target.value)}} type="email" placeholder=" " />
                    <input ref={password} onChange={(e)=>{setPasswordValue(e.target.value)}} type="password" placeholder=" " />
                    <button onClick={(e)=>{e.preventDefault();submit()}}> </button>
                    <h5><a onClick={()=>{registeration()}}> </a></h5>
                   
            </div>
                    </form>
                </div>
                <footer className='footer'>Copyright  2022</footer>
            </>
        )
    
}

export default Login