import React , {useEffect, useRef,useState} from 'react'
import './Admin.css'
import Main from './Component/Main/Main'
import Lecture from '../Admin/Component/Lecture/Lecture'
import Exam from '../Admin/Component/Exam/Exam'
import Task from '../Admin/Component/Task/Task'
import Student from './Component/Student/Student'
import Sent from './Component/Sent/Sent'
function Admin(){

    let [number,setNumber] = useState(1)
    let [name,setName] = useState("")

    let handler = ()=>{
        if(number === 1){
            return <Main/>
        }else if(number === 2){
            return <Lecture/>
        }else if(number === 3){
            return <Student/>
        }else if(number === 4){
            return(<Exam/>)
        }else if(number === 6){
            return(<Task/>)
        }else if(number === 7){
            return(<Sent/>)
        }
    }


    const nav = useRef()
    const content = useRef()
    const li1 = useRef()
    const li2 = useRef()
    const li3 = useRef()
    const li4 = useRef()
    const li6 = useRef()
    const li7 = useRef()

    useEffect(()=>{
        let name = JSON.parse(localStorage.getItem('teacher')).name
        setName(name)
    },[])
 

    return(
        <>
        <div class="lds-facebook" id="loader"><div></div><div></div><div></div></div>
    <div className="admin-nav">
    <h3> {name}</h3>
    <i onClick={()=>{


      let open =   nav.current.classList.contains('max-width') 
      let content2 = content.current.classList.contains('max-width2') 
      if(open){
          nav.current.classList.replace('max-width','zero-width')
      }else if(!open){
        nav.current.classList.replace('zero-width','max-width')
      }

      if(content2){
        content.current.classList.replace('max-width2','zero-width2')
    }else if(!content2){
        content.current.classList.replace('zero-width2','max-width2')
    }

    if(nav.current !== undefined){
        if(nav.current.classList.contains('zero-width')){
            li1.current.style.display = 'none'
            li2.current.style.display = 'none'
            li3.current.style.display = 'none'
            li4.current.style.display = 'none'
            li6.current.style.display = 'none'
            li7.current.style.display = 'none'
        }else{
            setTimeout(()=>{
                li1.current.style.display = 'block'
                li2.current.style.display = 'block'
                li3.current.style.display = 'block'
                li4.current.style.display = 'block'
                li6.current.style.display = 'block'
                li7.current.style.display = 'block'
            },400)
        }
    }

    }} className="fas fa-bars"></i>
    </div>


    <div className="admin-figure">
        <div ref={content} className="admin-content max-width2"> 
        {handler()}
        </div>

        <div ref={nav} className="admin-nav2 max-width" >
        <ul>
            <li className={number === 1 ? 'menu-select':null} onClick={()=>{setNumber(1)}}  ref={li1}></li>
            <li className={number === 2 ? 'menu-select':null} onClick={()=>{setNumber(2)}}  ref={li2} ></li>
            <li className={number === 3 ? 'menu-select':null} onClick={()=>{setNumber(3)}}  ref={li3}></li>
            <li className={number === 4 ? 'menu-select':null} onClick={()=>{setNumber(4)}}  ref={li4}></li>
            <li className={number === 6 ? 'menu-select':null} onClick={()=>{setNumber(6)}}  ref={li6}> </li>
            <li className={number === 7 ? 'menu-select':null} onClick={()=>{setNumber(7)}}  ref={li7}> </li>
        </ul>
        </div>
    </div>
        </>
    )
}

export default Admin