import React from 'react'
import { useHistory} from 'react-router-dom'
import './Nav.css'

function Nav() {

    let history = useHistory()

    let registration = ()=>{
        history.push('/registration')
    }

    let login = ()=>{
        history.push('/login')
    }
    return (
        <>
            <nav className='nav'>
            
                    <ul className="second-ul">
                        <li><a onClick={()=>{registration()}}> </a></li>
                        <li ><a onClick={()=>{login()}}> </a></li>
                    </ul>
               
            </nav>
        </>
    )

}

export default Nav