import React , {Component} from "react";
import './Test.css'
import axios from 'axios'
import Exam from '../Exam/Exam'


class Test extends Component{
    state = {
        numberOfI:this.props.numberOfI,
        answer:this.props.teacher.answers[this.props.numberOfI],
        now : 'test',
        err:null,
        timeDate:'',
        sec:'',
        min:'',
        hour:"",
        timing:this.props.timing
    }


    time(){
        let sec = this.state.sec;
        let min = this.state.min;
        let hour = this.state.hour;

        if(sec < 10){
            sec = '0' + sec
        }
        if(min <10){
            min = '0' + min
        }
        if(hour <10){
            hour = '0' + hour
        }

        return hour + ':' + min + ':' + sec
        
    }


    componentDidMount(){

   
        if(this.state.timing){
            let allMin =  (new Date(this.state.timing).getHours() - new Date().getHours())*60  + (new Date(this.state.timing).getMinutes() - new Date().getMinutes()) ;
            let hour = Math.floor(allMin /60);//0
            let min = Math.floor(allMin - (hour*60));//59
            let second = 0;//59
             
            this.setState({timing:this.props.timing,  hour:hour,min:min,sec:second })
            this.interval = setInterval(()=>{
                this.tick()
            },1000)
        }else{
            let allMin = this.props.date ;
            let hour = Math.floor(allMin /60);//0
            let min = Math.floor(allMin - (hour*60));//59
            let second = 0;//59
            
             this.setState({timing:this.props.timing,  hour:hour,min:min,sec:second })
             this.interval = setInterval(()=>{
                 this.tick()
             },1000)
        }
    }

    stop(){
        clearInterval(this.interval)
        this.setState({now:'exam'})

        // this.saveExam(); 
    }

    tick(){
        if(this.state.sec === 0){
            if(this.state.min === 0){
                if(this.state.hour === 0){
                    if( this.props.timing ){
                       if((new Date(this.props.timing).getTime()/1000/60 ) - (new Date().getTime()/1000/60) > 0){
                    
                    }else{
                           this.stop()
                       }
                    }else{
                        this.stop()
                    }
                }else{
                    this.setState({hour:this.state.hour -1 ,min:59,sec:59})
                }
            }else{
                this.setState({min:this.state.min -1 ,sec:59})
            }
        }else{
            this.setState({sec:this.state.sec -1 })
        }
    }
    
    render(){
      
let randomArr = []



    let saveExam = ()=>{
    let check = this.props.studentAnswers.map((n)=>{
      return n
    })
    
     if(check.length === this.state.answer.length){
        document.body.style.opacity = 0.9
        document.getElementById('loader').style.display = 'flex'
         let stuName =  JSON.parse( localStorage.getItem('student')).name;
         let degree = 0 ;
         let degreeOne = this.props.examDegree / this.state.answer.length
         for(let i=0;i<this.state.answer.length;i++){
             
              if(this.props.studentAnswers[i] === this.state.answer[randomArr[i]].value){
                  degree +=degreeOne;
              }
         }

         
              axios({
                  method:"post",
                  url:'/home/studegree',
                  data:{
                      stuName:stuName,degree:{name:this.props.examName,degree:degree}
                  }
              }).then(()=>{
                document.body.style.opacity = 1
                document.getElementById('loader').style.display = 'none'
                  this.setState({now:'exam'})
              })
     }
 }


   
    



   



    let viewExam = ()=>{
        let randomNum = Math.floor(Math.random() * this.state.answer.length) ;
        for(let i=0;i<this.state.answer.length;i++){
            while(randomArr.includes(randomNum)){
                randomNum = Math.floor(Math.random() * this.state.answer.length);
            }
            randomArr.push(randomNum)
        }


        let exams = [];
            if(this.props.typeOfExam === 'MCQ'){
     
                   for(let j=0;j<randomArr.length ;j++){
    
                    exams.push({
                        number:j,value:(
                            <ul className="admin-test" key={j}>
                                <h1 className="available-exam">  {j+1}</h1>
                                <li><input type="text" readOnly defaultValue={this.props.question[randomArr[j]].question} placeholder=" "/></li>
                                <li ><i onClick={()=>{
                                     this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer1
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }}
                                 className={`fas fa-circle-notch ${j}`}></i><input readOnly defaultValue={this.props.question[randomArr[j]].answer1} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                    this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer2
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${j}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }} className={`fas fa-circle-notch ${j}`}></i><input readOnly defaultValue={this.props.question[randomArr[j]].answer2} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                     this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer3
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[2].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${j}`)[3].classList.replace('fa-circle','fa-circle-notch')
                                }} className={`fas fa-circle-notch ${j}`}></i><input readOnly defaultValue={this.props.question[randomArr[j]].answer3} type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                      this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer4
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[2].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[3].classList.replace('fa-circle-notch','fa-circle')
                                }} className={`fas fa-circle-notch ${j}`}></i><input readOnly defaultValue={this.props.question[randomArr[j]].answer4} type="text" placeholder=""/></li>
                            </ul>
                        )
                    })
                   }
            
            }else if(this.props.typeOfExam === 'T/F'){

                for(let j=0;j<randomArr.length ;j++){
           
                    exams.push({
                        number:j,value:(
                            <ul className="admin-test" key={j}>
                                <h1 className="available-exam">  {j+1}</h1>
                                <li><input type="text"  defaultValue={this.props.question[randomArr[j]].question} readOnly placeholder=" "/></li>
                                <li ><i onClick={()=>{
                                 this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer1
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle-notch','fa-circle')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle','fa-circle-notch')}} className={`fas fa-circle-notch ${j}`}></i><input defaultValue={this.props.question[randomArr[j]].answer1} readOnly type="text" placeholder=""/></li>
                                <li ><i onClick={()=>{
                                this.props.studentAnswers[randomArr[j]] = this.props.question[randomArr[j]].answer2
                                 document.getElementsByClassName(`${j}`)[0].classList.replace('fa-circle','fa-circle-notch')
                                 document.getElementsByClassName(`${j}`)[1].classList.replace('fa-circle-notch','fa-circle')}} className={`fas fa-circle-notch ${j}`}></i><input defaultValue={this.props.question[randomArr[j]].answer2} readOnly type="text" placeholder=""/></li>
                            </ul>
                        )
                    })
            }
            
            }
    
            return exams.map((e)=>{return e.value})

    }
       
    

            return(
                this.state.now === 'test' ? ( <>
               {
                  <p className="nothing2">{ this.time()}</p>
               }
               
                    <div className="admin-test2">
                        {
                            viewExam()
                        }
                    </div>
            <div className='div-apload'>

                        <button className="admin-test-save" onClick={async()=>{
                            saveExam();
                        }}></button>
                        <p>{this.state.err ? this.state.err:null}</p>
                        </div>
             </>) : <Exam/> 
            )
        
                        
}
}
export default Test